* {
  /* border: 1px solid black; */
  margin: 0;
  padding: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.link {
  text-decoration: none;
  color: black;
}

.no-results {
  grid-column: 1 / col-end;
  text-align: center;
  padding: 50px;
  font-size: 2.5rem
}

.text-input, .button, label{
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.4rem;
}

.text-input {
  width: 400px;
  margin-left: 30px;
}

.button {
  background-color: lightskyblue
}

.no-results {
  grid-column: 1 / col-end;
  text-align: center;
  padding: 50px;
  font-size: 2.5rem
}

.header__container {
  background-color: lightblue;
}

.header__menu {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  font-size: 1.5em;
  text-transform: uppercase;
}

.header__menu-link {
  display: block;
  text-decoration: none;
  font-size: bold;
  color: black;
  padding: 20px 40px;
}

.header__menu-link:hover,
.header__active-link {
  color: red;
}
.showcase__container {
  padding: 0;
  margin: 20px;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr)
}

.showcase__movie {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}

.showcase__img {
  object-fit: contain;
  object-position: center;
  height: 300px;
}

.showcase__title {
  font-family: 'Oswald', sans-serif;
  display: grid;
  text-align: center;
  width: auto;
  height: 70px;
  font-size: 1.4rem;
  font-weight: bold;
  overflow: hidden;
  margin-bottom: 20px;
}
.pagination__container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  padding: 10px;
}

.pagination__current-page {
  padding: 10px;
  margin: 5px;
  font-size: 1.4rem;
}

.movie__img-container {
  margin-bottom: 25px;
}

.movie__radio {
  -webkit-transform: scale(1.75);
          transform: scale(1.75); 
}

.movie__details {
  display: grid;
  justify-items: center;
}

.movie__title {
  font-size: 2.5rem;
  text-align: center;
  padding: 20px;
}

.movie__img {
  width: 400px
}

.movie__detail {
  margin-bottom: 10px;
}

.movie__overview-title {
  font-size: 2rem;
  margin-bottom: 10px;
}

.movie__overview {
  font-size: 1.5em;
  padding: 20px 100px 50px 100px;
  text-align: justify;
}
.collection-list__container {
  padding: 10px 30px;
  font-size: 2rem;
  margin: 10px 50px;
}

.collection-list__item {
  padding: 20px;
}
.collection__header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center
}

.collection__name {
  padding: 30px 50px;
  font-size: 3rem;
}

.collection__movie {
  height: 500px;
  margin: 30px;
  display: grid;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
  grid-template: 5fr 1fr/ 1fr 3fr;
}

.collection__img-container {
  height: 100%;
  width: 350px;
  grid-row: 1 / 3;
  overflow: hidden;
}

.collection__img {
  width: 100%;
  height: auto;
  object-fit: contais;
  object-position: center;
}

.collection__title {
  font-size: 2.5em;
  text-align: left;
  margin-left: 75px;
}

.collection__overview {
  font-size: 1.4rem;
  padding: 20px 75px;
  text-align: justify
}
.collection__main-info {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.movie-settings__container {
  display: -webkit-flex;
  display: flex;
  grid-column: 2 / 3;
}

.movie-settings__rating {
  font-weight: bolder;
}

.movie-settings__rating {
  margin: 10px;
  padding: 10px;
  font-size: 1.4rem;
}
